<template>
    <div class="container">
        <div class="row">
            <div class="col-md-4">
                <div class="punchbox">
                    <h2 class="punchline">
                        <span>专注开发</span>
                        <span>创新</span>
                        <span>细节决定成败</span>
                    </h2>
                </div>
            </div>
            <div class="col-md-4" v-for="(item,index) in experienceList">
                <div class="service" >
                    <h4>{{item.title | substrFun(10)}}</h4>
                    <p>{{item.describe | substrFun(60)}}</p>
                    <a href="#" class="service-link puba">详情</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Experience",
        data(){
            return{
                experienceList:[]
            }
        },
        mounted() {
            const expJson = require('../../static/json/experience.json');
            this.experienceList=expJson.data;
        },
        /*created(){
          this.$axios.get("http://api.tianapi.com/txapi/ncovcity/index?key=561fb88bddd763468b0be3f773947733&date=2020-03-15")
                .then(res=>{
                    console.log(res);
                })
                .catch(error =>{
                    console.log(error)
                })
        },*/
        filters:{
            substrFun(value,num){
                // console.log(value,num);
                if(value.length>=num)
                {
                    return value.substr(0,num)+'...'
                }else{
                    return value
                }
            }
        }
    }
</script>

<style scoped>
.col-md-4{
    margin-bottom: 2.143rem;
}
.punchbox {
    padding: 90px 60px 65px;
    color: #605f65;
    text-align: left;
    line-height: 1;
}
.punchline {
    text-transform: none;
    font-family: 'Lora', serif;
    font-size: 15px;
}
.punchline span {
    display: block;
}
.punchline span:nth-of-type(1) {
    font-size: 33px;
}
.punchline span:nth-of-type(2) {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-size: 50px;
    color: #f95353;
    margin-top: 16px;
}
.punchline span:nth-of-type(3) {
    font-size: 20px;
    font-style: italic;
    margin-top: 26px;
}
.service {
    padding: 91px 45px 70px;
    background: #f7f7f7;
    cursor: pointer;
    -moz-transition: background 0.3s ease-in-out;
    -webkit-transition: background 0.3s ease-in-out;
    transition: background 0.3s ease-in-out;
}
.service:hover {
    color: #ffffff;
    background: #56c553;
}
.service:hover h4,
.service:hover .service-link {
    color: #ffffff;
}
.service h4 {
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
.service p {
    margin-top: 25px;
    line-height: 1.5;
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
.service .service-link {
    display: inline-block;
    color: #f95353;
    margin-top: 30px;
}
.service .service-link::after {
    background: #ffffff;
}
</style>



