<template>
    <div>
        <h1 style="display:none">{{$route.params.sel}}</h1>
        <WorkDetail :productData="productData"></WorkDetail>
    </div>
</template>

<script>
    import WorkDetail from "./WorkDetail";
    export default {
        name: "Work",
        components: {WorkDetail},
        data(){
            return{
                nowType:0,
                resJson:[],
                productData:[]
            }
        },
        created(){
            this.resJson = require('../../static/json/product.json');
            this.productData=this.resJson.data[0].list;
        },
        updated(){
            this.nowType=this.$route.params.sel;
            console.log(this.nowType);
            this.productData=this.resJson.data[this.nowType].list;
            // console.log(this.productData);
        }
    }
</script>

<style scoped>
img{
    width:100%;
    height: auto;
}
li{
    display: inline-block;
}
</style>



