<template>
    <div class="container">
        <div class="row">
            <div class="col-md-6">
                <h3>作品展{{getRouter}}</h3>
                <p>阶梯从来不是用来搁脚的，它只是让人们的脚放上一段时间，以便让别一只脚能够再往上登。</p>
            </div>
            <div class="col-md-6">
                <ul class="prods">
<!--                <li v-for="(item,index) in productBar" :class="{'active':nowIndex===index}" @click="nowIndex=index">-->
                    <li v-for="(item,index) in productBar" >
                        <router-link :to="{name:item.name,params:{sel:index}}" class="puba" exact>{{item.title}}</router-link>
                    </li>
                </ul>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
import Work from "./Work";
export default {
    name: "Product",
    components: {Work},
    data(){
        return{
            nowIndex:0,
            productBar:[
                {
                    title:'专题',
                    name:"Special",
                },
                {
                    title:'应用软件',
                    name:'Soft',
                },
                {
                    title:'网站',
                    name:'Web',
                },
                {
                    title:'App软件',
                    name:'Mobil',
                }
            ]
        }
    },
    computed:{
        getRouter(){
            this.active = this.$route.matched[0].path;
            if(this.active==''){
                this.active= '/';
            }
        }
    }
}
</script>

<style scoped>
.prods{
    text-align: right;
    margin-top: 3.071rem;
}
.prods li{
    display: inline-block;
    text-align: center;
    margin: 0.357rem 1.071rem;
}
.prods li a{
    line-height: 1.786rem;
    font-size: 1.143rem;
}
.prods li.active{
    border-bottom: 1px #2c3e50 solid;
}
.prods li.active a::after{
    background-color: transparent;
}
</style>



