<template>
    <div class="row">
        <div class="col-md-4" v-for="(item,index) in productData">
            <img :src="'../static/object/'+item.src" :title="item.title"/>
            <p>{{item.title}}</p>
            <a :href="item.url" target="_blank">详情</a>
        </div>
    </div>
</template>

<script>
    export default {
        name: "WorkDetail",
        data(){
            return{
            }
        },
        props:{
            productData: {
                type: Array,
                default: function () {
                    return[
                        {
                            title: "四宗最",
                            src:"sizongzui.jpg",
                            url: "http://qd.house.163.com/special/021014J8/qdsqszz.html"
                        }
                    ]
                }
            }
        }
    }
</script>

<style scoped>
img{
    width:100%;
    height: auto;
}
li{
    display: inline-block;
}
</style>



