import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Product from '@/components/Product'
import Experience from '@/components/Experience'
import About from '@/components/About'
import Work from '@/components/Work'
import Test from '@/components/Test'

Vue.use(Router)

export default new Router({
  linkActiveClass:"active",
  routes: [
    {
      path: '/',
      title:"主页",
      name: 'Home',
      component: Home
    },
    {
      path: '/Product',
      title:"作品",
      name: 'Product',
      component: Product,
      redirect:'/Product/Special',
      children:[
        {
          path:'/Product/Special',
          title:"专题",
          name:'Special',
          component:Work
        },
        {
          path:'/Product/Soft',
          title:"应用软件",
          name:'Soft',
          component:Work
        },
        {
          path:'/Product/Web',
          title:"网站",
          name:'Web',
          component:Work
        },
        {
          path:'/Product/Mobil',
          title:"app应用",
          name:'Mobil',
          component:Work
        }
      ]
    },
    {
      path: '/Experience',
      title:"项目经验",
      name: 'Experience',
      component: Experience
    },
    {
      path:'/About',
      title:"关于我",
      name:'About',
      component:About
    }
  ]
})



